'use client';

import { useEffect, useState } from 'react';
import DownloadAppButton from '../../components/DownloadAppButton';

interface User {
  id: string;
  firstName: string;
  lastName: string;
  companyName: string;
  email: string;
}

export default function Dashboard() {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await fetch('/api/auth/check');
        if (response.ok) {
          const data = await response.json();
          if (data.authenticated && data.user) {
            setUser(data.user);
          } else {
            // Redirect to login if not authenticated
            window.location.href = '/login';
          }
        } else {
          // Redirect to login if auth check fails
          window.location.href = '/login';
        }
      } catch (error) {
        console.error('Error fetching user:', error);
        window.location.href = '/login';
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, []);
  const handleLogout = async () => {
    try {
      await fetch('/api/auth/logout', { method: 'POST' });
      window.location.href = '/login';
    } catch (error) {
      console.error('Logout error:', error);
    }
  };

  if (loading) {
    return (
      <main style={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        minHeight: '100vh',
        fontSize: '18px',
        color: '#6b7280'
      }}>
        Laden...
      </main>
    );
  }

  return (
    <main style={{ textAlign: 'center', padding: '40px 20px' }}>
      <div style={{ 
        position: 'absolute', 
        top: '20px', 
        right: '20px' 
      }}>
        <button
          onClick={handleLogout}
          style={{
            backgroundColor: '#ef4444',
            color: 'white',
            padding: '8px 16px',
            border: 'none',
            borderRadius: '6px',
            fontSize: '14px',
            fontWeight: '500',
            cursor: 'pointer',
            transition: 'background-color 0.2s ease'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.backgroundColor = '#dc2626';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.backgroundColor = '#ef4444';
          }}
        >
          Logout
        </button>
      </div>
      
      {/* Welcome Message */}
      {user && (
        <div style={{ 
          marginBottom: '32px',
          padding: '20px',
          backgroundColor: '#f0f9ff',
          borderRadius: '12px',
          border: '1px solid #bae6fd'
        }}>
          <h1 style={{ 
            fontSize: '28px', 
            fontWeight: '600', 
            color: '#0f172a',
            margin: '0 0 8px 0'
          }}>
            Welkom {user.firstName}!
          </h1>
          <p style={{ 
            fontSize: '16px', 
            color: '#64748b',
            margin: '0'
          }}>
            {user.companyName} ‚Ä¢ {user.email}
          </p>
        </div>
      )}

      <div style={{ marginBottom: '48px' }}>
        <h2 style={{ fontSize: '32px', fontWeight: 'bold', marginBottom: '16px', color: '#1f2937' }}>
          AI-Powered Meeting Notes
        </h2>
        <p style={{ fontSize: '18px', color: '#6b7280', marginBottom: '32px', maxWidth: '600px', margin: '0 auto 32px' }}>
          Record meetings, get instant AI transcriptions, and automatically email summaries to all attendees. 
          Now available as a mobile app for easy access anywhere.
        </p>
        
        <div style={{ 
          display: 'flex', 
          gap: '20px', 
          justifyContent: 'center', 
          alignItems: 'center',
          flexWrap: 'wrap',
          marginBottom: '48px'
        }}>
          <DownloadAppButton />
          
          <a 
            href="/meetings/new" 
            style={{
              display: 'inline-flex',
              alignItems: 'center',
              gap: '8px',
              backgroundColor: '#3b82f6',
              color: 'white',
              padding: '16px 24px',
              textDecoration: 'none',
              borderRadius: '12px',
              fontSize: '16px',
              fontWeight: '600',
              minHeight: '56px',
              transition: 'all 0.2s ease'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#2563eb';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = '#3b82f6';
            }}
          >
            <span>üéôÔ∏è</span>
            Start New Meeting
          </a>
        </div>
      </div>

      <div style={{ 
        display: 'grid', 
        gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))', 
        gap: '24px',
        maxWidth: '900px',
        margin: '0 auto'
      }}>
        <div style={{ 
          backgroundColor: '#f8fafc', 
          padding: '24px', 
          borderRadius: '12px',
          textAlign: 'left'
        }}>
          <h3 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '12px', color: '#1f2937' }}>
            üé§ Easy Recording
          </h3>
          <p style={{ color: '#6b7280', lineHeight: '1.6' }}>
            One-click recording with real-time chunking and automatic cloud processing.
          </p>
        </div>

        <div style={{ 
          backgroundColor: '#f8fafc', 
          padding: '24px', 
          borderRadius: '12px',
          textAlign: 'left'
        }}>
          <h3 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '12px', color: '#1f2937' }}>
            ü§ñ AI Summaries
          </h3>
          <p style={{ color: '#6b7280', lineHeight: '1.6' }}>
            Powered by OpenAI for accurate transcriptions and intelligent meeting summaries.
          </p>
        </div>

        <div style={{ 
          backgroundColor: '#f8fafc', 
          padding: '24px', 
          borderRadius: '12px',
          textAlign: 'left'
        }}>
          <h3 style={{ fontSize: '20px', fontWeight: '600', marginBottom: '12px', color: '#1f2937' }}>
            üìß Auto Email
          </h3>
          <p style={{ color: '#6b7280', lineHeight: '1.6' }}>
            Automatic email delivery of meeting notes to all participants via SendGrid.
          </p>
        </div>
      </div>
      
      <footer style={{ 
        marginTop: '64px', 
        padding: '24px', 
        borderTop: '1px solid #e5e7eb',
        fontSize: '14px',
        color: '#6b7280',
        textAlign: 'center'
      }}>
        <p style={{ marginBottom: '8px' }}>
          Door AI Notulist te gebruiken, gaat u akkoord met het verwerken van audio-opnames voor 
          transcriptie en AI-samenvatting doeleinden.
        </p>
        <p>
          <a href="/privacy" style={{ color: '#3b82f6', textDecoration: 'underline' }}>
            Lees onze privacyverklaring
          </a> voor meer informatie over gegevensbescherming en uw rechten.
        </p>
      </footer>
    </main>
  );
}